.login-page {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #131921;
}

@media (max-width: 1024px) {
  .login-page {
    flex-direction: column;
  }
}

.login-card {
  background-color: rgba(255, 255, 255, 0.91);
  box-shadow: 0px 0px 10px darkgray;
  width: 45%;
  height: 40vh;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px;
  max-width: 500px;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

input {
  width: 100%;
  height: 7vh;
  border: 1px solid rgba(128, 128, 128, 0.428);
  margin-bottom: 7px;
  font-size: 1.2em;
  padding: 5%;
  border-radius: 5px;
}

.btn {
  width: 100%;
  background-color: #131921;
  font-size: 1.2em;
  padding: 2%;
  border-radius: 5px;
  margin-bottom: 0.8%;
}

.btn:hover {
  color: #131921;
  background-color: rgba(255, 255, 255, 0.91);
  border: 2px solid #131921;
}

.google {
  background-color: #4285f4;
}
